import React, { useReducer, useContext, useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import { getFirestore, collection, getDocs } from 'firebase/firestore';
import { initializeApp } from 'firebase/app';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Container, Row, Col, Navbar, Nav, NavDropdown } from 'react-bootstrap';

function Home() {
  return <h2>Home Page jojo </h2>;
}

function About() {
  return <h2>About Page</h2>;
}

function Contact() {
  return <h2>Contact Page</h2>;
}

function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  const handleLogin = () => {
    // Simula un inicio de sesión
    setIsAuthenticated(true);
  };

  const handleLogout = () => {
    // Simula un cierre de sesión
    setIsAuthenticated(false);
  };



  return (
    <Router>
      <div>
        <Navbar bg="light" expand="lg">
          <Container>
            <Navbar.Brand href="/">MyApp</Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="me-auto">
                <Nav.Link href="/">Home</Nav.Link>
                <Nav.Link href="/about">about</Nav.Link>
                <Nav.Link href="/contact">contact</Nav.Link>
              </Nav>
              <Nav>
                {isAuthenticated ? (
                  <>
                    <Nav.Link href="#logout" onClick={handleLogout}>Logout</Nav.Link>
                  </>
                ) : (
                  <>
                    <Nav.Link href="#login" onClick={handleLogin}>Login</Nav.Link>
                    <Nav.Link href="#register">Register</Nav.Link>
                  </>
                )}
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
        {/* Definir las rutas actualizadas con Routes y element */}
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/about" element={<About />} />
          <Route path="/contact" element={<Contact />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
